* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 700px;
    margin-inline: auto;
}

button {
    width: 50px;
    height: 50px;
    padding: 10px;
    cursor: pointer;
}

img {
    width: 100%;
    height: 100%;
}